// Core styles

body {
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #333;
    font-size: 1.1em;
    line-height: 1.5;
    text-align: center;
}

img {
    display: block;
    width: 100%;
    height: auto;
}

h1, h2, h3 {
    margin: 0;
    padding: 1em 0;
}

p {
    margin: 0;
    padding: 1em 0;
}

.btn {
    display: inline-block;
    background: #333;
    color: #fff;
    text-decoration: none;
    padding: 1em 3em;
    border: 1px solid #666;
    margin: .5em 0;
}

.btn:hover {
    background: #eaeaea;
    color: #333;
}

// Header

#showcase {
    min-height: 450px;
    color: #fff;
    text-align: center;

    .bg-image {
        position: absolute;
        background: #333 url("../images/pexels-photo-248515.png");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        height: 450px;
        z-index: -1;
        opacity: 0.6;
    }

    h1 {
        padding-top: 100px;
        padding-bottom: 0;
    }

    .content-wrap {
        padding: 0 1.5em;
    }
}

